<template>
    <v-card>
        <v-card-title class="d-flex justify-center">
            Adicionar Provento
        </v-card-title>

        <v-form ref="form" v-model="valid" lazy-validation>
            <v-col cols="12" class="d-flex justify-start">
                <v-row>
                    <v-col cols="6">
                        <v-select :items="categorias" v-model="categoria" dense label="Fonte"
                            :rules="categoriaRules"></v-select>
                    </v-col>
                    <v-col cols="6">
                        <v-text-field id="money" label="" v-model="valorReal" prefix="R$" dense type="text" :rules="valorRules" @keyup="filtrarMoney('money')" @blur="filtrarMoney('money')" @focusin="filtrarMoney('money')">
                        </v-text-field>
                    </v-col>
                </v-row>
            </v-col>
        </v-form>

        <v-card-actions>
            <v-btn color="blue darken-1" text @click="fechar">
                Fechar
            </v-btn>
            <v-spacer></v-spacer>
            <v-btn :disabled="!valid" color="blue darken-1" text @click="adicionarProvento">
                Salvar
            </v-btn>
        </v-card-actions>
    </v-card>
</template>

<script>
export default {
    props: {
        changeDialog: {
            required: true,
            type: Function
        }
    },
    data() {
        return {
            categorias: ['Alugueis', 'Investimentos', 'Pensão', 'Salário', 'Outros'],
            categoria: '',
            valorReal: '',
            valid: true,
            categoriaRules: [
                valor => !!valor || 'Preencha este campo!'
            ],
            valorRules: [
                valor => !!valor || 'Preencha este campo!'
            ],
        }
    },   
    methods: {
        adicionarProvento() {
            this.$refs.form.validate()
            if (this.categoria !== '' && this.categoria !== null && this.valorReal !== '' && this.valorReal !== null) {
                let valor = this.filtrarMoney('', this.valorReal)
                valor = valor.replace('.', '').replace(',', '.')                
                this.$store.commit('adicionarProvento', {
                    categoria: this.categoria,
                    valor: valor
                })
                this.changeDialog(false)
                this.$refs.form.reset()
            }
        },
        fechar() {
            this.changeDialog(false)
            this.categoria = ''
            this.valorReal = ''
            this.$refs.form.reset()
        }
    }
}
</script>

